import { useQuery } from "@tanstack/react-query";
import { getAllGalleryBanners, getBanners } from "../api/bannerApis";

const useBanners = (params, options) => {
  return useQuery({
    queryKey: ["banners", params],
    queryFn: () => getBanners(params),
    staleTime: 1000 * 60 * 5,
    retry: false,
    ...options,
  });
};

const useGalleryBanners = () => {
  return useQuery({
    queryKey: ["galleryBanners"],
    queryFn: () => getAllGalleryBanners(),
    staleTime: 1000 * 60 * 5,
  });
};

export { useBanners, useGalleryBanners };
